import React                     from 'react'
import Nav                       from '../../Components/NavBar/Nav'
import HeroPage                  from '../../Components/HeroPage/HeroPage'
import { Container, Typography } from '@material-ui/core'
import { useTranslation }        from 'react-i18next'
import { Link }                  from 'react-router-dom'
import Footer                    from '../../Components/Footer/Footer'
import { useStyles }             from '../Styles/OrganizationStyle'

const Home = () => {
  const { t }   = useTranslation()
  const classes = useStyles()
  return (<>
      <HeroPage nameImg="home.jpg" positionImg={'center center'}>
        <Nav/>
        <div className="hero-container">
          <div className={'hero-home-content '}>
            <h1 className={classes.titleHero}>{t('titleHeroHome')}</h1>
          </div>
        </div>
      </HeroPage>
      <section>
        <Container>
          <h2 className={` ${classes.titleResponsive}`}>{t('titleSection2Home')}</h2>
          <div className="section-about">
            <Typography component="p">{t('contentSection2Home')}</Typography>
          </div>
        </Container>
      </section>
      <section className="section-gallery">
        <Container maxWidth="lg">
          <h2 className={classes.titleResponsive}>{t('titleSection1Home')}...</h2>
          <div className="gallery">
            <div className="img-1">
              <img src="/assets/galleryHome/1.jpg" alt="baby shower organis par Estelle Rouill"/>
            </div>
            <div className="img-2">
              <img src="/assets/galleryHome/2.jpg" alt="anniversaire organis par Estelle Rouill"/>
            </div>
            <div className="img-3">
              <img src="/assets/galleryHome/3.jpg" alt="atelier organis par Estelle Rouill"/>
            </div>
            <div className="img-4">
              <img src="/assets/galleryHome/4.jpg" alt="illustration d'un mariage organis par Estelle Rouill"/>
            </div>
            <div className="img-5">
              <img src="/assets/galleryHome/5.jpg"
                   alt="Estelle Rouill met en place les dcoration pour une crmonie"/>
            </div>
            <div className="img-6">
              <img src="/assets/galleryHome/6.jpg" alt="Deux marie main dans la main"/>
            </div>
            <div className="img-7">
              <img src="/assets/galleryHome/7.jpg" alt="Demande en mariage homme  sa femme"/>
            </div>
          </div>
          <Link to="/galerie" className="btn-home">Voir plus de photos</Link>
        </Container>
      </section>
      
      <Footer/>
    </>
  )
}

export default Home
